<script src="../../../../../yijjixhaxun/vue/src/network/adminRequest.js"></script>
<template>
	<div id="headerBox">
		<div class="header">	
			<div class="header_left">
		        <div class="hLeft">
		            <div class="hLp">
		                <p>隐迹查询服务平台</p>
		            </div>
		        </div>
		    </div>
		    <!-- 右边登录 -->
		    <div class="header_right">
		        <div class="hRbox">
		            <div class="hfirst">
		                <div class="hRname">
							<p class="hrmoder_to" v-if='$store.state.adminuser.name'>{{$store.state.adminuser.name}}</p>
							<p class="hrmoder" @click="abuner" v-else>请登录</p>
							<div class="upKuang">
								<div class="bluesj">
									<div class="jjttbb"></div>
								</div>
								<ul>
									<li><a href="javascript:;" @click="soulert">修改密码</a></li>
									<li><a href="javascript:;" @click="setout">退出登录</a></li>
								</ul>
							</div>
		                </div>
		                <div class="Help">
		                    <p>帮助</p>
		                </div>
		                <div class="unPlie">
			                <div class="triangle">
			                    <div class="triangle_to"></div>
			                </div>
			                <ul>
			                    <li><a href="javascript:;" class="Administration" @click="Adminis">管理密钥</a></li>
			                </ul>
			            </div>
		                
		            </div>
		            
		        </div>
		    </div>
		</div>
		<!--管理密钥-->
		<div class="Modal">
	        <div class="modelto Adtion" v-if="secret">
	            <div class='listr_li'>
	                <div class="Adtion_t">
	                    <p>管理密钥</p>
	                </div>
	                <div class="sronly-xs">
	                	<div class="security">
	                		<p>密钥在传输过程中保护数据安全</p>
	                	</div>
	                	<div class="pacc">
	                		<label for="">隐迹服务公钥:</label>
	                		<a href="javascript:;" @click="down">下载</a>
	                	</div>
	                    <div class="form-group plert" style="margin-top: 0!important;">
	                        <button class="cancel"  @click="Suret">取消</button>
	                    </div>
	                </div>
	            </div>
	        </div>
	    </div>
	</div>
</template>

<script>
	import {loginOut} from "@/network/admin";
	import {config} from "@/common/config";

	export default {
		name: "headerBox",
		data() {
	      return {
	        value: true,
			secret:false
	      }
	    },
		created: function () {
			this.peroiong();
		},
		activated:function(){
			this.peroiong();
		},
		methods: {
			setout(){
				var name = this.$store.state.adminuser.name;
				var data ={
					userName: this.$store.state.adminuser.name,
				}
				loginOut(data).then(res => {
		        	console.log(res)
		        	if (res.code== 0) {
                 	var user={'name':''};
                 	this.$store.commit('setdataadminuser',user)
                 	window.localStorage.removeItem('vuex-along');
                 	layer.msg("退出成功！", {
	                        icon: 1,
	                        time: 1500
	                    });
	                    this.$router.push("/Charging/Login")
	                } else if(res.code !== 0){
	                    layer.msg(res.msg, {
	                        icon: 2,
	                        time: 1500
	                    });

	                }
		        })
			},
			//下载服务端公钥
			down(){
				// if (res.code) {
				// 	layer.msg(res.msg, {
				// 		icon: 2,
				// 		time: 1500
				// 	});
				// 	return false
				// }
				window.location.href = config().url + '/key/downSerPubKey';

			},
			
			Adminis(){
				this.secret= true;
			},
			Suret(){
				this.secret= false;
			},
			soulert(){
				this.$router.push({ path: '/Charging/ServicePassword' });
			},
			//请登录退出
			abuner(){
				this.$router.push("/Charging/Login")
			},
			peroiong: function(){
				this.perionth = this.$store.state.permissions.permissions;
				console.log()
				if(this.perionth.length == 1){
					$(".Help>p").css('display','none');
					$('.upKuang').css('right','55px')
				}
				if(this.perionth.length == 2){
					$(".Help>p").css('display','none');
					$('.upKuang').css('right','55px')
				}
				if(this.perionth.length == 3){
					$(".Help>p").css('display','none');
					$('.upKuang').css('right','55px')
				}
				if(this.perionth.length == 4){
					$(".Help>p").css('display','block');
					$('.upKuang').css('right','83px')
				}
				for(let key  in this.perionth){
					var permissions = this.perionth[key];
					this.pentionger = permissions.permitId;

				}
			},
		}
		
	}
</script>

<style>
	@import "~@assets/css/user/common.css";
	@import "~@assets/css/user/header.css";
</style>