import  axios from 'axios'
axios.defaults.sendSuscced = true;  // 初始化报错后的请求状态
axios.defaults.retryDelay = 1000*60*5;
export function request(config) {
    const instance = axios.create({
//      baseURL:'http://192.168.98.20:8081',
//         baseURL:'http://192.168.99.31:8083',
//         baseURL:'https://192.168.98.37',
        // baseURL:'http://192.168.99.172:8083/',
     // baseURL : window.global_config.BASE_URL,
		baseURL: '/yjselect-api',
//      timeout:1000*60*5,

    })

    //axios 拦截器 提交到服务器前
    instance.interceptors.request.use(config =>{
        return config
    },error => {
        return error
    })
    //axios 拦截器 相应拦截
    instance.interceptors.response.use(res =>{

        return res.data
    },error => {
        // 根据传回来的错误信息`erroe`进行判断
        // 意为：`error.code`
        if (error.code === 'ECONNABORTED' && error.message.indexOf('timeout') !== -1) {
            var newHttp= new Promise(function (resolve){
                resolve()
            })
            // `newHttp实例执行完成后会再次执行`
            // 返回一个promise实例，同时重新发起请求，config请求配置，包扩请求头和请求参数
            return newHttp.then(function (){
                return axios(config)
            })
        }
        // 若不是超时,则返回未错误信息
        return Promise.reject(error)

    })

    return instance(config)

}